  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: 0;
}
.boton-amarillo:hover {
  background-color: #be7208;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: 0;
}
.boton-amarillo-block:hover {
  background-color: #be7208;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-flex;
  border: 0;
}
.boton-verde:hover {
  background-color: #5a8d00;
}

.boton-esp {
  background-color: #1f2024;
  padding: 0.5rem 1rem;
  border-radius: 2rem;
  transition: all 0.5s ease-in-out;
}
.boton-esp:hover {
  color: #FFFFFF;
