}

.boton-volver {
  max-width: 15rem;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 1rem;
  border: none;
  color: #FFFFFF;
  border-radius: 3rem;
  cursor: pointer;
}
.boton-volver i {
  color: #FFFFFF;
  font-size: 3rem;
}
.boton-volver i:hover {
  color: #e1e1e1;
}
.boton-volver:hover {
  color: #e1e1e1;
}
.boton-volver.azul {
  background-color: #1b324b;
}
.boton-volver.azul:hover {
  background-color: #0e1926;
}
.boton-volver.naranja {
  background-color: #E08709;
}
.boton-volver.naranja:hover {
  background-color: #af6907;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .anuncio {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .anuncio .precio {
  color: #71B100;
}
.dark-mode .anuncio .icono {
  filter: invert(100%);
}
.dark-mode .resumen-propiedad .precio {
  color: #71B100;
}
.dark-mode .resumen-propiedad .icono {
  filter: invert(100%);
}
.dark-mode .formulario label, .dark-mode .formulario legend, .dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]), .dark-mode .formulario textarea, .dark-mode .formulario select {
  background-color: #333333;
  color: #FFFFFF;
}
.dark-mode blockquote {
  color: #FFFFFF;
}
.dark-mode .carta {
  background-color: #1f2024;
  box-shadow: 3px 6px 8px #070809;
}
.dark-mode .carta a {
  color: #FFFFFF;
}
.dark-mode .carta .icono-carta {
  color: gray;
}
.dark-mode .propiedad, .dark-mode .vendedor {
  background-color: #1f2024;
}
.dark-mode .vacio {
  background-color: #1f2024;
}
.dark-mode .campo-obligatorio p {
  color: #f40000;
}
.dark-mode .login-card-header h1 {
  color: #FFFFFF;
}
.dark-mode .login-card::after {
  background-color: #333333;
  box-shadow: inset 20px 20px 20px #303030;
}
.dark-mode .login-card::before {
  background-image: conic-gradient(#00dce2 20deg, transparent 120deg);
}
.dark-mode .login-card form .form-item i {
  color: #333333;
}
.dark-mode .login-card-logo i {
  color: #e1e1e1;
}

/** UI **/
.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 0 0 3rem;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
  min-height: 40rem;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 65rem;
  }
}
.header.inicio .contenido-header {
  min-height: 40rem;
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 65rem;
  }
}

.contenido-header {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .contenido-header h1 {
    font-size: 4rem;
    text-align: left;
  }
}

.titulo-header {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: auto;
  display: flex;
  justify-content: center;
}
.titulo-header h1 {
  text-align: center;
}

.footer {
  margin: 0;
  background-color: #1b324b;
  width: 100%;
}
.footer .contenido-footer {
  padding-top: 2.5rem;
  justify-content: center;
  align-items: center;
}
.footer .contenido-footer .a-nav {
  display: block;
  align-items: center;
  justify-content: center;
  width: auto;
}
.footer .contenido-footer .a-nav nav {
  display: block;
}
@media (min-width: 768px) {
  .footer .contenido-footer .a-nav nav {
    padding: auto;
    max-width: 100%;
    width: auto;
    display: flex;
    justify-content: space-around;
  }
}
.footer p {
  color: #FFFFFF;
  text-align: center;
  margin: 0;
  padding-bottom: 1rem;
}

.contenido-barra {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 1rem 0;
}
@media (min-width: 768px) {
  .contenido-barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.barra-fija {
  margin: 0;
  padding: 1rem 0;
  width: 100%;
  transition: all 0.5s ease;
}
.barra-fija.activo {
  padding: 0;
  background-color: #1b324b;
}

.barra .no-inicio {
  background-color: #1b324b;
}

.barra {
  position: fixed;
  width: 100%;
  z-index: 2;
}

@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 1.6rem;
  display: flex;
  flex-direction: column;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }
}
.navegacion a:hover {
  color: #E08709;
}

.toggle-btn {
  margin: auto 0;
  display: block;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
@media (min-width: 768px) {
  .toggle-btn {
    display: none;
  }
}

.fa-solid {
  color: #FFFFFF;
  font-size: 3rem;
}

.logo img {
  padding: auto;
}

.menu-desplegable {
  position: relative;
  z-index: 10;
  display: block;
  justify-content: center;
  align-items: center;
  height: 0;
  margin-top: 0;
  width: 80%;
  background: rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(15px);
  border-radius: 10px;
  overflow: hidden;
  transition: height 1s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
@media (min-width: 768px) {
  .menu-desplegable {
    display: none;
  }
}
@media (min-width: 768px) {
  .menu-desplegable .navegacion {
    display: none;
  }
}
.menu-desplegable .navegacion a:hover {
  background-color: #E08709;
  color: #FFFFFF;
}
.menu-desplegable.auth.open {
  width: 80%;
  height: 21rem;
}
.menu-desplegable.open {
  width: 80%;
  height: 18rem;
}

.fija {
  display: none;
}
@media (min-width: 768px) {
  .fija {
    display: flex;
  }
}

.container {
  background-color: #FFFFFF;
  position: relative;
  width: 6rem;
  height: 3rem;
  box-shadow: inset 4px 4px 6px #e1e1e1, inset -4px -4px 6px #FFFFFF;
  cursor: pointer;
  border-radius: 2.5rem;
}

.toggle {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 3rem;
  height: 3rem;
  transform: scale(0.7);
  background-color: linear-gradient(145deg, #d8d8d8, #FFFFFF);
  border-radius: 2.5rem;
  box-shadow: 3px 3px 8px #c4c4c4, -3px -3px 8px #FFFFFF;
  transition: all 0.5s ease;
}

.active {
  box-shadow: inset 4px 4px 6px #1f2024, inset -4px -4px 6px #373a40;
  background-color: #373a40;
}
.active .toggle {
  left: 3rem;
  box-shadow: 4px 4px 10px #1f2024, -4px -4px 10px #161616;
}

.container2 {
  background-color: #FFFFFF;
  position: relative;
  width: 6rem;
  height: 3rem;
  box-shadow: inset 4px 4px 6px #e1e1e1, inset -4px -4px 6px #FFFFFF;
  cursor: pointer;
  border-radius: 2.5rem;
  margin: auto;
  margin-top: 1.5rem;
}

.toggle2 {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 3rem;
  height: 3rem;
  transform: scale(0.7);
  background-color: linear-gradient(145deg, #d8d8d8, #FFFFFF);
  border-radius: 2.5rem;
  box-shadow: 3px 3px 8px #c4c4c4, -3px -3px 8px #FFFFFF;
  transition: all 0.5s ease;
}

.active2 {
  box-shadow: inset 4px 4px 6px #1f2024, inset -4px -4px 6px #373a40;
  background-color: #373a40;
}
.active2 .toggle2 {
  left: 3rem;
  box-shadow: 4px 4px 10px #1f2024, -4px -4px 10px #161616;
}

@media (min-width: 768px) {
  .iconos-nosotros {
